@import url(https://fonts.googleapis.com/css2?family=Viaoda+Libre&display=swap);
header{background-color:brown;color:#fff;position:relative;right:1%;bottom:21px;height:100px;width:1371px}h1{text-align:center;position:relative;top:25px;font-style:italic}span ul li{position:relative;bottom:10px;font-size:x-large;display:inline;border-width:thin;border-color:#fff;padding:2em}
.form-url{margin-left:35%}.label-name{font-size:25px}.input-form{width:30%;height:25px}.input-submit{width:11%;height:30px;position:relative;left:2%}.text-line{border-style:double;height:350px;width:60%;position:relative;left:300px}.method-button{margin-left:40%;margin-top:1%}.post-method{margin-left:2%}.put-method{margin-left:2%}.delete-method{margin-left:2%}
.loading-page{margin-left:35%;border:16px solid #f3f3f3;border-radius:50%;border-top:16px solid blue;border-right:16px solid green;border-bottom:16px solid red;border-left:16px solid #000;width:120px;height:120px;-webkit-animation:spin 2s linear infinite;animation:spin 2s linear infinite}@-webkit-keyframes spin{0%{-webkit-transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg)}}@keyframes spin{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.form{margin-left:35%}.data{margin-left:35%;margin-top:1%;border-style:double;height:auto;width:auto}
body{font-family:"Viaoda Libre",cursive;font-size:large}
footer{background-color:#999;position:fixed;bottom:0;left:0%;width:100%}p{text-align:center;font-size:x-large}
